$bg-color : #282c34;
$font-color: #ccc;
$name-color: #fff;

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

    background-color: #{$bg-color};
    color:#{$font-color};

}

.intro .name {
    display: inline-flex;
    color: #{$font-color};
}

.intro {
    display: flex;
	flex-direction: column;
    margin: auto;
    min-height: 100vh;
    justify-content: center;
    align-items: center;

}

.intro h1 {
    font-size: 3.5rem;
    margin-bottom: 5px;
}

.intro h2 {
    font-size: 1.5rem;
}

.wrapper {
    width: 100%;
    height: 100vh;
}

.flex {
    display: flex;
	flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    min-height: 80%;
}

hr {
    margin: 20px;
    color: #ccc;
}

.about {
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    gap: 20px;
}

.about_blurb {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 400px;
}

.about_splitter {
    background-color: #ccc;
    width: 2px;
    height: 25vh;
}

.about_face {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    max-width: 50vw;
}

.socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    gap: 20px;
}

FaChevronDown {
    position: absolute;
    bottom: 40px;

}